h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@media (min-width: 1400px) {
  .header {
    color: #fff;
    background-color: #2c2c2c;
    border-radius: 0 0 30px 30px;
    justify-content: right;
    width: 100%;
    height: 800px;
    font-family: Poppins, sans-serif;
    display: -ms-flexbox;
    display: flex;
  }

  .header__cover {
    grid-template-rows: 120px 680px;
    grid-template-columns: 40% 60%;
    -ms-flex-align: center;
    align-items: center;
    width: 80%;
    min-width: 916px;
    height: 100%;
    margin-left: 54px;
    display: grid;
  }

  .header__cover--top {
    -ms-flex-direction: row;
    flex-direction: row;
    grid-column: span 2;
    gap: 135px;
    padding-right: 40px;
    display: -ms-flexbox;
    display: flex;
  }

  .header__cover--top--logo {
    width: 46px;
    height: 28px;
    transition: all .3s;
    display: inline-block;
  }

  .header__cover--top--logo:hover {
    transform: scale(1.05);
  }

  .header__cover--top--menu {
    display: none;
  }

  .header__cover--top--wrap {
    justify-content: space-evenly;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
  }

  .header__cover--top--wrap--nav {
    -ms-flex-align: center;
    align-items: center;
    gap: 48px;
    width: 100%;
    max-width: 890px;
    display: -ms-flexbox;
    display: flex;
  }

  .header__cover--top--wrap--nav--links {
    color: #fff;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    line-height: 150%;
    text-decoration: none;
    transition-duration: .3s;
  }

  .header__cover--top--wrap--nav--links:hover {
    color: #2060f6;
    transform: scale(1.1);
  }

  .header__cover--top--wrap--hire {
    color: #fff;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    width: 70px;
    font-size: 13px;
    font-weight: 700;
    line-height: 150%;
    text-decoration: none;
    transition-duration: .3s;
  }

  .header__cover--top--wrap--hire--line {
    border: 1px solid #2060f6;
    border-radius: 2px;
    width: 64px;
    height: 0;
  }

  .header__cover--top--wrap--hire:hover {
    color: #2060f6;
    transform: scale(1.1);
  }

  .header__cover--left--header {
    letter-spacing: -2px;
    margin: 0 0 32px;
    font-size: 64px;
    font-weight: 600;
    line-height: 68px;
  }

  .header__cover--left--text {
    margin: 0 0 73px;
    font-family: Open Sans, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }

  .header__cover--left--button {
    text-align: center;
    color: #fff;
    background-color: #2060f6;
    border-radius: 8px;
    margin-top: 10px;
    padding: 14px 90px;
    font-size: 15px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: .3s;
    display: inline-block;
  }

  .header__cover--left--button:hover {
    border: 2px solid #f2f6fa;
    transform: scale(1.03);
  }

  .header__cover--right {
    box-sizing: border-box;
    border-radius: 30px 0;
    height: 680px;
  }

  .header__cover--right--background {
    object-fit: cover;
    border-radius: 30px 0;
    width: 100%;
    height: 100%;
    transition: all .3s;
    display: none;
    position: relative;
    left: 1100px;
  }

  .header__cover--right--background:first-child {
    display: block;
    left: 0;
  }

  .header__cover--right--content {
    z-index: 2;
    box-sizing: border-box;
    grid-template-rows: 70px;
    grid-template-columns: 80px 265px;
    gap: 24px;
    padding: 560px 48px 48px;
    display: grid;
    position: absolute;
    top: 120px;
  }

  .header__cover--right--content--arrows {
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-align: center;
    align-items: center;
    gap: 16px;
    display: -ms-flexbox;
    display: flex;
  }

  .header__cover--right--content--arrows--link {
    background-color: #fff;
    border: none;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    transition: all .3s;
  }

  .header__cover--right--content--arrows--link--pic {
    width: 18px;
    height: 18px;
    padding-top: 3px;
  }

  .header__cover--right--content--arrows--link:hover {
    box-shadow: 0 0 3px 2px #fff;
  }

  .header__cover--right--content--expalined--header {
    letter-spacing: 3px;
    text-transform: uppercase;
    margin: 0 0 8px;
    font-size: 13px;
    font-weight: 700;
  }

  .header__cover--right--content--expalined--text {
    margin: 0;
    font-family: Open Sans, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }

  .slide-transition {
    animation-name: change;
    animation-duration: .6s;
    animation-iteration-count: 1;
    left: 0;
  }

  @keyframes change {
    from {
      left: 1100px;
    }

    to {
      left: 0;
    }
  }

  .close {
    display: none;
  }
}

